<template>
<div class="cityWrap">
    <header>
        <router-link to="/home">
            <i class="el-icon-arrow-left"></i>
        </router-link>
        <div>{{cityinfo?cityinfo.name:''}}</div>
        <router-link to="/home">切换城市</router-link>
    </header>
    <div class="form">
        <div>
            <input type="text" placeholder="输入学校、商务楼、地址" v-model="inputV">
        </div>
        <div>
            <div class="searchBtn" @click="searchPlace">提交</div>
        </div>
    </div>
    <div class="historyTitle" v-if="isShowHistoryHeader">搜索历史</div>
    <ul>
        <li v-for="p in placeArr" :key="p.geohash" @click="choosePlace(p)">
            <h4 class="p_name ellipsis">{{p.name}}</h4>
            <p class="ellipsis p_address">{{p.address}}</p>
        </li>
    </ul>
    <div class="clearHistory" v-if="isShowClearBtn" @click="clearAllPlace">清空所有</div>
</div>
</template>
<script>
export default {
name:"City",
data(){
    return{
cityId:'0',
cityinfo:null,
inputV:'',
placeArr:[],
isShowHistoryHeader:true,
isShowClearBtn:false
    }
},
created(){
    this.cityId = this.$route.params.cityId;
    this.axios.get("/v1/cities/" + this.cityId).then(res=>{
     this.cityinfo = res
    }).catch(err=>{});
    if (localStorage.getItem("placeHistory")) {
        this.placeArr = JSON.parse(localStorage.getItem("placeHistory"))
        this.isShowClearBtn = true;
    }
},
methods:{
    searchPlace(){
        this.axios.get("/v1/pois",{
            params:{
                city_id:this.cityId,
                keyword:this.inputV,
                type:"search"
            }
        }).then(res=>{
            console.log(res)
            this.placeArr = res;
            this.isShowHistoryHeader = false;
        }).catch(err=>{})
    },
    choosePlace(p){
        // 第一次存先创建一个数组，将p放进数组，将数组存放到localstorage
        // 以后存，获取数组，将p放进数组，将数组存放到localstorage
        this.$router.push('/msite')
        localStorage.setItem("placeNameHistory",p.name);
        if (!localStorage.getItem("placeHistory")) {
            let hisArr = [];
            hisArr.push(p);
            localStorage.setItem("placeHistory",JSON.stringify(hisArr));
        }else{
            let hisArr = JSON.parse(localStorage.getItem("placeHistory"));
            // 去重
            let newArr = hisArr.filter(v=>{
                return v.name == p.name
            })
            if (!newArr.length) {
                hisArr.push(p);
                localStorage.setItem("placeHistory",JSON.stringify(hisArr));
            }
        }
    },
    clearAllPlace(){
        localStorage.removeItem("placeHistory");
        this.placeArr=[];
        this.isShowClearBtn = false;
    }
}
}
</script>
<style scoped>
.cityWrap{
    padding-top: 2.35rem;
}
header>a:first-child{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 0.65rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
header>a:last-child{
  position: absolute;
  right: 0.4rem;
  color: #fff;
  font-size: 0.6rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
.form>div{
    width: 90%;
    margin: 0 auto;
    text-align: center;
}
.form{
    background-color: #fff;
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
    padding-top: .4rem;
}
.form>div:nth-child(1)>input{
    border: 1px solid #e4e4e4;
    padding: 0 .3rem;
    font-size: .65rem;
    color: #333;
    border-radius: .1rem;
    margin-bottom: .4rem;
    width: 100%;
    height: 1.4rem;
    box-sizing: border-box;
    outline: none;
}
.searchBtn{
    background-color: #3190e8;
    font-size: .65rem;
    color: #fff;
    border-radius: .1rem;
    margin-bottom: .4rem;
    width: 100%;
    height: 1.4rem;
    line-height: 1.4rem;
}
.historyTitle{
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
    padding-left: .5rem;
    font: .475rem/.8rem Microsoft YaHei;
}
.clearHistory{
    font-size: .7rem;
    color: #666;
    text-align: center;
    line-height: 2rem;
    background-color: #fff;
}
.p_name{
    margin: 0 auto .35rem;
    width: 90%;
    font-size: .65rem;
    color: #333;
}
.p_address{
    width: 90%;
    margin: 0 auto .55rem;
    font-size: .45rem;
    color: #999;
}
ul{
    background-color: #fff;
}
ul li{
    margin: 0 auto;
    padding-top: .65rem;
    border-bottom: 1px solid #e4e4e4;
}
</style>